body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.player-model {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #fff;
  border-radius: 10px;
  
  margin-left: -5px;
  margin-top: -5px;
}

.player-model .gun {
  position: absolute;
  width: 3px;
  height: 10px;
  top: 4px;
  left: 4px;
}

.scene {
  margin-top: 20px;
  width: 400px;
  height: 400px;
  border: 1px solid black;
  background: #333;
  position: relative;
}


.scene .trail-dot {
  position: absolute;
  width: 2px;
  height: 2px;
  background-color: #fff;
  border-radius: 10px;
  
  margin-left: -1px;
  margin-top: -1px;
}

.scene .trail-dot.definitive {
  background-color: #f00;
}



.scene .definitive-dot {
  position: absolute;
  width: 8px;
  height: 8px;
  border: 1px solid #fff;
  border-radius: 10px;
  
  margin-left: -4px;
  margin-top: -4px;
}

.server {
  display: inline-block;
  padding: 20px;
}
.client {
  display: inline-block;
  padding: 20px;
}
